/* Circle */
.circle {
  height: 100px;
  width: 100px;
  border-radius: 50%;
  /* background-color: #ff4343; */
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.circle:before,
.circle:after {
  content: '';
  display: block;
  position: absolute;
  top: 0; 
  right: 0; 
  bottom: 0; 
  left: 0;
  border: 1px solid rgb(207,181,59);
  border-radius: 50%;
}


.circle:before {
  -webkit-animation: ripple 4s linear infinite;
          animation: ripple 4s linear infinite;
}

.circle:after {
  -webkit-animation: ripple 4s linear 1s infinite;
          animation: ripple 4s linear 1s infinite;
}

@-webkit-keyframes ripple{
  0% { -webkit-transform: scale(1); transform: scale(1); }
  50% { -webkit-transform: scale(1.3); transform: scale(1.3); opacity:1; }
  100% { -webkit-transform: scale(1.6); transform: scale(1.6); opacity:0; }
}

@keyframes ripple{
  0% { -webkit-transform: scale(1); transform: scale(1); }
  50% { -webkit-transform: scale(1.3); transform: scale(1.3); opacity:1; }
  100% { -webkit-transform: scale(1.6); transform: scale(1.6); opacity:0; }
